<template>
    <section id="section-game" :class="concertbg">
        <!--  -->
        <transition name="bounce">
            <div class="score_outer" v-if="scoreboard">
                <transition-group class="heart_outer" name="bounce" tag="div">
                    <div v-for="item in heart" :key="item">
                        <img src="../assets/img/heart.png" alt="" class="heart">
                    </div>
                </transition-group>
            </div>
        </transition>
        <!--  -->
        <transition name="fade" :duration="1000">
            <img src="../assets/img/talk_boy.png" id="character" v-show="lan_file.talk0[i].charactershow" v-if="datesex == 'boy' && character ">
        </transition>
        <transition name="fade" :duration="1000">
            <img src="../assets/img/talk_girl.png" id="character" v-show="lan_file.talk0[i].charactershow" v-if="datesex == 'girl' && character ">
        </transition>
        <transition name="fade" :duration="1000">
            <img src="../assets/img/talk_boy.png" id="character" v-show="lan_file.talk2[m].charactershow" v-if="datesex == 'boy' && character ">
        </transition>
        <transition name="fade" :duration="1000">
            <img src="../assets/img/talk_girl.png" id="character" v-show="lan_file.talk2[m].charactershow" v-if="datesex == 'girl' && character ">
        </transition>
    <!--  start -->
    <transition name="fade" :duration="1000">
        <div class="talk-block" v-if="talkblock">
            <div class="name-block">{{lan_file.talk0[i].name}}</div>
            <p>{{lan_file.talk0[i].content}}</p>
            <transition  name="gonext" mode="out-in">
                <button id="next" @click="gonext" class="btn btn-talknext" v-show="talkbtn">>></button>
            </transition>
        </div>
    </transition>

    <transition name="fade" :duration="1000">
        <div class="talk-block" v-if="talk2block">
            <div class="name-block">{{lan_file.talk2[m].name}}</div>
            <p>{{lan_file.talk2[m].content}}</p>
            <transition  name="gonext" mode="out-in">
                <button id="next" @click="gonext2" class="btn btn-talknext" v-show="talkbtn">>></button>
            </transition>
        </div>
    </transition>


    <transition name="fade" :duration="1000">
        <div v-if="nggame" class="nggame_block">
            <div v-for="(game,index) in gamePlay" :key="game.id">
                <div v-show="u == index">
                    <img :src="game.imgurl" alt="" class="ngimg">
                    <h4 class="question">{{game.question}}</h4>
                    <div class="answer">
                        <button class="gameoption" v-for="item in game.answerarray" :key="item.id" @click="heartchange(game.id,item.score)">
                            {{item.answer}}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </transition>
    <!-- murmur -->
    <transition name="fade" :duration="1000">
        <div class="talk-block" v-if="murmurblock">
            <p>{{lan_file.talk1[o]}}</p>
            <transition name="gonext" mode="out-in">
                <button id="next" @click="nextgame" class="btn btn-talknext" v-show="talkbtn">>></button>
            </transition>
        </div>
    </transition>
    <!--  end -->
    <transition name="fade" :duration="1000">
        <div class="clap_outer" v-if="clap" @click="claphand()">
            <button class="btn_clap" ></button>
        </div>
    </transition>
    




</section>
</template>
<script>
    export default {
        name:'SectionGame',
        props:['name','datesex','lan','concert','concertname'],
        data(){
            return{
            sectionid: 'end',
            talkblock: true,//
            talk2block: false,
            murmurblock:false,
            talkbtn:true,
            i:0,//
            u:0,//
            o:0,//murmur
            m:0,//
            outsideimg: true,
            character: false,//
            scoreboard:false,
            heart: 0,//
            firstheart: true,//
            nggame:false,
            clap: false,
            lan_ch:{
                talk0:[
                {
                    name: `${this.name}`,
                    content: '()',
                    charactershow: false
                },
                {
                    name: '',
                    content: '',
                    charactershow: false
                },
                {
                    name: '',
                    content: '',
                    charactershow: false
                }
                ],
                talk1:[
                '',
                '',
                '',
                '',
                ''
                ],
                talk2:[
                {
                    name: '',
                    content: '',
                    charactershow: true
                },
                {
                    name: '',
                    content: '',
                    charactershow: true
                },
                {
                    name: '',
                    content: '?',
                    charactershow: true
                }
                ]
            },
            lan_en:{
                talk0:[
                {
                    name: `${this.name}`,
                    content: 'EN()',
                    charactershow: true
                },
                {
                    name: 'Date person',
                    content: 'EN',
                    charactershow: true
                },
                {
                    name: 'Date person',
                    content: 'EN',
                    charactershow: true
                }
                ],
                talk1:[
                'en',
                'en',
                'en',
                'en',
                'en'
                ] 
            },
            pushMurmurData:{
                mur_ch:['',''],
                mur_en:['en','en']
            },
            gamelang:[],
            gameUsed:[],
            gamePlay:[],
            gameAll: {
                game_ch:[
                {
                    id: 0,
                    question: '',
                    answerarray: [
                    {answer:'',score:1},
                    {answer:'',score:0},
                    {answer:'',score:-1}
                    ],
                    imgurl: require('../assets/img/ngpic_throat.jpg')
                },
                {
                    id: 1,
                    question: '',
                    answerarray: [
                    {answer:'',score:0},
                    {answer:'',score:1},
                    {answer:'',score:-1}
                    ],
                    imgurl: require('../assets/img/ngpic_phone.jpg')
                },
                {
                    id: 2,
                    question: '',
                    answerarray: [
                    {answer:'',score:1},
                    {answer:'',score:-1},
                    {answer:'',score:0}
                    ],
                    imgurl: require('../assets/img/ngpic_door.jpg')
                },
                {
                    id: 3,
                    question: '',
                    answerarray: [
                    {answer:'',score:0},
                    {answer:'',score:-1},
                    {answer:'',score:1}
                    ],
                    imgurl: require('../assets/img/ngpic_phone.jpg')
                },
                {
                    id: 4,
                    question: '',
                    answerarray: [
                    {answer:'',score:0},
                    {answer:'',score:-1},
                    {answer:'/',score:1}
                    ],
                    imgurl: require('../assets/img/ngpic_talk.jpg')
                },
                {
                    id: 5,
                    question: '',
                    answerarray: [
                    {answer:'',score:-1},
                    {answer:'',score:0},
                    {answer:'',score:1}
                    ],
                    imgurl: require('../assets/img/ngpic_drop.jpg')
                },
                {
                    id: 6,
                    question: '',
                    answerarray: [
                    {answer:'',score:-1},
                    {answer:'',score:1},
                    {answer:'',score:0}
                    ],
                    imgurl: require('../assets/img/ngpic_flower.jpg')
                },
                {
                    id: 7,
                    question: '',
                    answerarray: [
                    {answer:'',score:1},
                    {answer:'',score:-1},
                    {answer:'',score:0}
                    ],
                    imgurl: require('../assets/img/ngpic_tempo.jpg')
                },
                {
                    id: 8,
                    question: '',
                    answerarray: [
                    {answer:'',score:1},
                    {answer:'',score:0},
                    {answer:'',score:-1}
                    ],
                    imgurl: require('../assets/img/ngpic_clap.jpg')
                },
                ],
                game_en:[
                {
                    id: 0,
                    question: 'EN',
                    answerarray: [
                    {answer:'',score:1},
                    {answer:'',score:0},
                    {answer:'',score:-1}
                    ],
                    imgurl: require('../assets/img/ngpic_throat.jpg')
                },
                {
                    id: 1,
                    question: '',
                    answerarray: [
                    {answer:'',score:0},
                    {answer:'',score:1},
                    {answer:'',score:-1}
                    ],
                    imgurl: require('../assets/img/ngpic_phone.jpg')
                },
                {
                    id: 2,
                    question: 'EN',
                    answerarray: [
                    {answer:'',score:1},
                    {answer:'',score:-1},
                    {answer:'',score:0}
                    ],
                    imgurl: require('../assets/img/ngpic_door.jpg')
                },
                {
                    id: 3,
                    question: 'EN',
                    answerarray: [
                    {answer:'',score:0},
                    {answer:'',score:-1},
                    {answer:'',score:1}
                    ],
                    imgurl: require('../assets/img/ngpic_phone.jpg')
                },
                {
                    id: 4,
                    question: 'EN',
                    answerarray: [
                    {answer:'',score:0},
                    {answer:'',score:-1},
                    {answer:'/',score:1}
                    ],
                    imgurl: require('../assets/img/ngpic_talk.jpg')
                },
                {
                    id: 5,
                    question: 'EN',
                    answerarray: [
                    {answer:'',score:-1},
                    {answer:'',score:0},
                    {answer:'',score:1}
                    ],
                    imgurl: require('../assets/img/ngpic_drop.jpg')
                },
                {
                    id: 6,
                    question: 'EN',
                    answerarray: [
                    {answer:'',score:-1},
                    {answer:'',score:1},
                    {answer:'',score:0}
                    ],
                    imgurl: require('../assets/img/ngpic_flower.jpg')
                },
                {
                    id: 7,
                    question: 'EN',
                    answerarray: [
                    {answer:'',score:1},
                    {answer:'',score:-1},
                    {answer:'',score:0}
                    ],
                    imgurl: require('../assets/img/ngpic_tempo.jpg')
                },
                {
                    id: 8,
                    question: 'EN',
                    answerarray: [
                    {answer:'',score:1},
                    {answer:'',score:0},
                    {answer:'',score:-1}
                    ],
                    imgurl: require('../assets/img/ngpic_clap.jpg')
                },
                ]
            }
        }
    },
    methods:{
        gonext(){
            var vobj=this;
            if(this.i < this.lan_file.talk0.length-1){ //i=0,i=1
                this.i++;
                this.talkbtn = false;
                setTimeout(function(){vobj.talkbtn =true; }, 1000);
                console.log('click'+ this.i + 'length:'+ this.lan_file.talk0.length);
            }
            if(this.i == this.lan_file.talk0.length-1){
                this.heartboardappear();
                this.arrangegame();
                console.log(this.gamePlay);
                this.talkblock = false;
                setTimeout(function(){vobj.nggame = true; console.log(vobj.nggame)}, 2000);
                this.$parent.$emit('updategameid', this.gamePlayID);
                this.$parent.$emit('updatemusicid', this.musicid);
            }
        },
        nextgame(){
            var vobj=this;
            this.murmurblock = false;
            if(this.o<this.lan_file.talk1.length-1){
            setTimeout(function(){vobj.nggame = true; }, 500);

            }else{
            setTimeout(function(){vobj.clap = true; }, 3000);
            this.$parent.$emit('updategamemusicpause');
            }
        },
        gonext2(){
            var vobj=this;
            if(this.m < this.lan_file.talk2.length){
                this.m++;
                this.talkbtn = false;
                setTimeout(function(){vobj.talkbtn =true; }, 1000);
                console.log('click'+ this.i + 'length:'+ this.lan_file.talk2.length);
            }
            if(this.m == this.lan_file.talk2.length){
                console.log();
                this.talk2block = false;
                this.$parent.$emit('updatesection', this.sectionid);
                // setTimeout(function(){vobj.nggame = true; console.log(vobj.nggame)}, 2000);
            }
            
        },
        heartchange(id,scr){
            var vobj=this;
            this.heart +=scr;
            if(this.heart > 12 ){
                this.heart = 12;
            }
            if(this.heart < 0){
                this.heart = 0;
            }
            this.o++;
            this.u++;
            this.nggame = false
            setTimeout(function(){vobj.murmurblock = true; }, 500);
            if(id==5&&scr!==1){
                this.lan_file.talk1.splice(3,1,this.pushMurmur[0]);
            }
        },
        heartrun(){
            setInterval(this.heartappear, 150);
        },
        heartboardappear(){
            this.scoreboard = true;
            this.heartrun();
        },
        heartappear(){
            if(this.heart < 6 && this.firstheart){
                this.heart++;
            }else{
                clearInterval(this.heartrun);
                this.firstheart = false;
                
            }
        },
        claphand(){
            var vobj = this;
            this.heart++;//++
            this.$parent.$emit('updateheart', this.heart);
            this.clap = false;
            this.clap = false;
            setTimeout(function(){vobj.talk2block = true;vobj.character = true }, 7000);
            console.log(this.gamePlayID);
            this.$parent.$emit('updatemusicid', 7);

        },
        arrangegame(){
            
                var check5 = true;
                var check6 = true;
                var check7 = true;
                var check8 = true;
            for(let i = 0; i < this.gamePlay.length; i++) {
                if (this.gamePlay[i].id === 5 && check5) {
                    this.gamePlay.splice(this.gamePlay.findIndex(e => e.id===5),1);
                    check5 = false;
                }
                if (this.gamePlay[i].id === 7 && check7) {
                    this.gamePlay.splice(this.gamePlay.findIndex(e => e.id===7),1);
                    check7 = false;
                }
                if (this.gamePlay[i].id === 8 && check8) {
                    this.gamePlay.splice(this.gamePlay.findIndex(e => e.id===8),1);
                    check8 = false;
                }
                if (this.gamePlay[i].id === 6 && check6) {
                    this.gamePlay.splice(this.gamePlay.findIndex(e => e.id===6),1);
                    check6 = false;
                }
            }
            if(check5 == false){
                this.gamePlay.unshift(this.gamelang[5]);
            }
            if(check7 == false){
                this.gamePlay.push(this.gamelang[7]);
            }
            if(check8 == false){
                this.gamePlay.push(this.gamelang[8]);
            }
            if(check6 == false){
                this.gamePlay.push(this.gamelang[6]);
            }
        }
    },
    mounted(){
        // var vobj = this;
        if(this.lan == 'ch'){
            this.gamelang = this.gameAll.game_ch;
            console.log('this.gamelang');
            console.log(this.gamelang);
        }else{
            this.gamelang = this.gameAll.game_en;
            console.log('this.gamelang');
            console.log(this.gamelang);
        }
        
        switch(this.concert){
            case 1:
                this.gameUsed = this.gamelang.slice(0,7);
                this.musicid = 3;
                console.log('this.gameUsed');
                console.log(this.gameUsed);
            break;
            case 2:
                this.gameUsed = this.gamelang.slice(0,9);
                this.musicid = 4;
                console.log('this.gameUsed');
                console.log(this.gameUsed);
            break;
            case 3:
                this.gameUsed = this.gamelang.slice(0,9);
                this.musicid = 5;
                console.log('this.gameUsed');
                console.log(this.gameUsed);
            break;
            case 4:
                this.gameUsed = this.gamelang.slice(0,7);
                this.musicid = 6;
                console.log('this.gameUsed');
                console.log(this.gameUsed);
            break;
        }

        for(let index=0;index<5;index++){
            let n=Math.floor(Math.random()*this.gameUsed.length);
            this.gamePlay.push(this.gameUsed[n]);
            this.gameUsed.splice(n,1);//
        }
        
        this.lan_file.talk1.push(this.pushMurmur[1]);//murmur
        console.log('gamePlay');
        console.log(this.gamePlay);
        console.log(this.lan_file.talk1);
    },
    computed:{
        lan_file:function(){    
            if(this.lan =="ch"){
                return this.lan_ch;
            }else{
                return this.lan_en;
            }
        },
        concertbg:function(){
            return `emptyconcert${this.concert}`;
        },
        pushMurmur:function(){    
            if(this.lan =="ch"){
                return this.pushMurmurData.mur_ch;
            }else{
                return this.pushMurmurData.mur_en;
            }
        },
        gamePlayID:function(){
            let IDs =[];
            let vobj = this;
            for(let i =0;i<this.gamePlay.length;i++){
                IDs.push(vobj.gamePlay[i].id)
            }
            return IDs;
        }

}
}
</script>
<style>
#section-game.emptyconcert1{
    background-image: url(../assets/img/play01.jpg);
}
#section-game.emptyconcert2{
    background-image: url(../assets/img/full2.jpg);
}
#section-game.emptyconcert3{
    background-image: url(../assets/img/full3.jpg);
}
#section-game.emptyconcert4{
    background-image: url(../assets/img/play04.jpg);
}
img#character{
    position: absolute;
    z-index: 1;
}
#section-game .score_outer{
    position: absolute;
    top: 20px;
    right: 20px;
}
#section-game .heart_outer{
    width: 350px;
    height: 39px;
    padding: 10px 19px 7px 19px;
    background-image: url(../assets/img/heart_outer.png);
    display: flex;
    flex-direction: row-reverse;
}
#section-game .heart_outer .heart{
    margin-left: 5px;
}
#section-game .ngimg{
    position: absolute;
    width: 330px;
    top: 90px;
    left: 50%;
    transform: translateX(-50%);
}
#section-game .nggame_block .question {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 12px 30px;
    width: 900px;
    height: 97px;
    left: 50%;
    top: 430px;
    text-align: center;
    font-size: 1.5rem;
    background-color: #252525c4;
    box-shadow: 0 2px 8px -2px #391414;
    transform: translateX(-50%);
    color: #FFF;
}
#section-game .nggame_block .answer {
    position: absolute;
    display: flex;
    justify-content: space-between;
    width: 900px;
    left: 50%;
    top: 310px;
    transform: translateX(-50%);
}
#section-game .nggame_block .gameoption {
    width: 32%;
    display: inline-block;
    font-size: 22px;
    padding: 15px 17px;
    border: none;
    box-shadow: inset 0 0 6px 0 #583526;
    border-radius: 20px;
    background-color: rgb(153, 109, 75);
    color: #ffffff;
    font-weight: bold;
}
#section-game .clap_outer .btn_clap{
    position: absolute;
    background-image: url(../assets/img/clap.png);
    background-size: contain;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    width: 140px;
    height: 140px;
    background-color: transparent;
    border: none;
}
.bounce-enter-active {
    animation: bounce-in .5s;
}
.bounce-leave-active {
    animation: bounce-in .5s reverse;
}
@keyframes bounce-in {
    0% {
        transform: scale(0);
    }
    50% {
        transform: scale(1.2);
    }
    100% {
        transform: scale(1);
    }
}
</style>