<template>
    <section id="section-intro" :class="concertbg">
        <!--  -->
        <transition name="fade" :duration="1000">
        <img src="../assets/img/talk_boy.png" id="character" v-show="lan_file.talk0[i].charactershow" v-if="datesex == 'boy' && character ">
        </transition>
        <transition name="fade" :duration="1000">
        <img src="../assets/img/talk_girl.png" id="character" v-show="lan_file.talk0[i].charactershow" v-if="datesex == 'girl' && character ">
        </transition>
        <!--  start -->
        <transition name="fade" :duration="1000">
            <div class="talk-block" v-if="talkblock">
                <div class="name-block">{{lan_file.talk0[i].name}}</div>
                <p>{{lan_file.talk0[i].content}}</p>
                <transition  name="gonext" mode="out-in">
                    <button id="next" @click="gonext" class="btn btn-talknext" v-show="talkbtn">>></button>
                </transition>
            </div>
        </transition>
        <!--  end -->
        <!--  start-->
        <div class="concert_intro_block" v-if="introconcert">
            <button type="button" class="btn btn-next" @click.prevent="gonext2"></button>
            <h2>{{concertintro.title}}</h2>
            <p>{{concertintro.content}}</p>
        </div>
        <!--  end-->
        <!--  start-->
        <transition name="fade">
            <div class="intro_block" :class="concertbg" v-if="intropart">
                <!--  start-->
                <transition-group  name="fade" tag="div">
                    <div v-for="item in concertintros2" :key="item.btn" class="musicimg_outer" :class="item.btn">
                        <img :src="item.imgurl" alt=""   class="musicimg" :class="{show:item.imgshow}" >
                    </div>
                </transition-group>
                <!--  end-->
                <!--  start-->
                <div :class="concertintros2class">
                    <button :id="item.btn" class="btn-intro" v-for="(item, index) in concertintros2" :key="index" @click.prevent="openwindow(index)" :class="{active: item.imgshow}">
                    </button>
                </div>
                <!--  end-->
                <!--  start-->
                <transition name="fade">
                    <div class="concert_intro_block" v-if="intropartwindow">
                        <img :src="musicalInstrument.infoimgurl" alt="">
                        <button type="button" class="btn btn-next" @click.prevent="gonext3"></button>
                    </div>
                </transition>
                <!--  end-->
            </div>
        </transition>
        <!--  end-->
        <!--  start-->
        <transition name="fade">
            <div class="done_block" v-if="done" :class="concertbg">
            </div>
        </transition>
        <!--  end-->

