             }
             if(this.i ==2){
                this.talkblock = false;
                setTimeout(function(){vobj.talkblock =true; }, 1200);
                this.outsideimg =false;
                this.$parent.$emit('updatemusicid', 2);
             }
            if(this.i == this.lan_file.talk0.length - 5){
                this.introconcert = true;
                this.talkblock = false;
            }
            if(this.i == this.lan_file.talk0.length - 4){
                this.intropart = true;
            }
            if(this.i == this.lan_file.talk0.length - 3){
                this.talkblock = false;
                this.$parent.$emit('updatemusicid', 1);
            }
            if(this.i == this.lan_file.talk0.length){
                this.character = false;
                this.talkblock = false;
                this.$parent.$emit('updatesection', this.sectionid);
                console.log('done');
             }
        },
        gonext2(){
            this.talkblock = true;
            this.introconcert = false;
        },
        openwindow(val){
            this.o = val;
            if(this.concertintros2[this.o].imgshow == false){
                this.intropartwindow = true;
            }else{
                console.log('this intro is done')
            }
        },
        gonext3(){
            this.intropartwindow = false;
            this.u++;
