    computed:{
    lan_file:function(){    
        if(this.lan =="ch"){
            return this.lan_ch;
        }else{
            return this.lan_en;
        }
    },
    concertbg:function(){
        if(this.outsideimg){
            return "outside";
        }else{
            return `emptyconcert${this.concert}`;
            }
    },
    concertintro_lan:function(){
        if(this.lan =="ch"){
            return this.concertintros.ch
        }else{
            return this.concertintros.en
        }
    },
    concertintro:function(){
        return this.concertintro_lan[this.concert-1];
    },
    concertintros2class:function(){
        return `concert${this.concert}`;
    },
    concertintros2:function(){
        return this.concerts[this.concert-1];
    },
    musicalInstrument:function(){
        if(this.lan =="ch"){
            return this.concertintros2[this.o].ch
        }else{
            return this.concertintros2[this.o].en
        }
        
