<template>
    <section id="section-end" :class="endbg">
        <div class="end-block">
            <div class="end-text">
                <div class="title">
                    <h4 v-if="endtitle">{{endtitle}}</h4>
                    <p v-if="endcontent">{{endcontent}}</p>
                </div>
                <a class="again">
                    <h5><img src="../assets/img/return.png" alt=""> </h5>
                    <p>!</p>
                </a>
            </div>
            <div class="end-links">
                <div class="right">
                    <button class="link how" @click="gameopen"></button>
                    <a class="link search"></a>
                    <a href="" class="fb"><img src="../assets/img/fb.png" alt=""> </a>
                </div>
                <div class="left">
                    <a href="" class="muzik"><img src="../assets/img/muzik.jpg" alt="" width="115" height="115"></a>
                    <button class="link staff"  @click="staffopen"></button>
                </div>
            </div>
        </div>
        <div id="gamewindow" class="answer-block">
            <div class="cross" @click="gameclose"><img src="../assets/img/cross.png" alt=""></div>
            <div class="game_answer">
                <div class="question_outer" v-for="item in lan_file.game" :key="item.id" :class="{done:item.doneclass}">
                    <div class="img"><img :src="item.imgurl" alt=""></div>
                    <div class="content">
                        <h6>{{item.question}}</h6>
                        <p>{{item.content}}</p>
                    </div>
                </div>
            </div>
        </div>
        <div id="staffwindow" class="staff-block" @click="staffclose">
            <div class="cross" ><img src="../assets/img/cross.png" alt=""></div>
            <h6></h6>
            <div class="text">
                <div class="left">
                    <p></p>
                    <P></P>
                    <P></P>
                </div>
                <div class="right">
                    <p>muzik </p>
                    <p>mia </p>
                    <p></p>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
export default {
    name:'SectionEnd',
    props:['lan','heart','gameid'],
    data() {
        return {
            lan_file:[],
            endcontent:'',
            endtitle:'',
            lan_ch:{
                end_title:['(','',''],
                end_content:[
                    '',
                    '',
                    ''],
                game:[
                    {
                        id: 0,
                        question: '',
                        content:'',
                        imgurl: require('../assets/img/small_throat.png'),
                        doneclass: false
                    },
                    {
                        id: 1,
                        question: '',
                        content:'',
                        imgurl: require('../assets/img/small_phone.png'),
                        doneclass: false
                    },
                    {
                        id: 2,
                        question: '',
                        content:'',
                        imgurl: require('../assets/img/small_door.png'),
                        doneclass: false
                    },
                    {
                        id: 3,
                        question: '',
                        content:'',
                        imgurl: require('../assets/img/small_phone.png'),
                        doneclass: false
                    },
                    {
                        id: 4,
                        question: '',
                        content:'',
                        imgurl: require('../assets/img/small_talk.png'),
                        doneclass: false
                    },
                    {
                        id: 5,
                        question: '',
                        content:'',
                        imgurl: require('../assets/img/small_drop.png'),
                        doneclass: false
                    },
                    {
                        id: 6,
                        question: '',
                        content:'',
                        imgurl: require('../assets/img/small_flower.png'),
                        doneclass: false
                    },
                    {
                        id: 7,
                        question: '',
                        content:'',
                        imgurl: require('../assets/img/small_tempo.png'),
                        doneclass: false
                    },
                    {
                        id: 8,
                        question: '',
                        content:'',
                        imgurl: require('../assets/img/small_clap.png'),
                        doneclass: false
                    }
                ]
            },
            lan_en:{
                end_title:['EN(','EN','EN'],
                end_content:[
                    'EN',
                    'EN',
                    'EN'],
                game:[
                    {
                        id: 0,
                        question: 'EN',
                        content:'00',
                        imgurl: require('../assets/img/small_throat.png'),
                        doneclass: false
                    },
                    {
                        id: 1,
                        question: 'EN',
                        content:'01',
                        imgurl: require('../assets/img/small_phone.png'),
                        doneclass: false
                    },
                    {
                        id: 2,
                        question: 'EN',
                        content:'02',
                        imgurl: require('../assets/img/small_door.png'),
                        doneclass: false
                    },
                    {
                        id: 3,
                        question: 'EN',
                        content:'03',
                        imgurl: require('../assets/img/small_phone.png'),
                        doneclass: false
                    },
                    {
                        id: 4,
                        question: 'EN',
                        content:'04',
                        imgurl: require('../assets/img/small_talk.png'),
                        doneclass: false
                    },
                    {
                        id: 5,
                        question: 'EN',
                        content:'05',
                        imgurl: require('../assets/img/small_drop.png'),
                        doneclass: false
                    },
                    {
                        id: 6,
                        question: 'EN',
                        content:'06',
                        imgurl: require('../assets/img/small_flower.png'),
                        doneclass: false
                    },
                    {
                        id: 7,
                        question: 'EN',
                        content:'07',
                        imgurl: require('../assets/img/small_tempo.png'),
                        doneclass: false
                    },
                    {
                        id: 8,
                        question: 'EN',
                        content:'08',
                        imgurl: require('../assets/img/small_clap.png'),
                        doneclass: false
                    }
                ]
            }
        }
    },
    mounted(){
        if(this.lan == 'ch'){
            this.lan_file = this.lan_ch;
            console.log(this.lan_file)
        }else{
            this.lan_file = this.lan_en;
        }

        if(this.heart>9){
            this.endtitle = this.lan_file.end_title[0];
            this.endcontent = this.lan_file.end_content[0];
            this.$parent.$emit('updatemusicid', 8);
        }else if(this.heart>6 && this.heart<=9){
            this.endtitle = this.lan_file.end_title[1];
            this.endcontent = this.lan_file.end_content[1];
            this.$parent.$emit('updatemusicid', 9);
        }else{
            this.endtitle = this.lan_file.end_title[2];
            this.endcontent = this.lan_file.end_content[2];
            this.$parent.$emit('updatemusicid', 10);
        }
        
        for(let i=0;i<this.gameid.length;i++){
            var j = this.gameid[i];
            this.lan_file.game[j].doneclass = true;
            console.log(j)
        }
    },
    methods:{
        gameopen(){
            var gamewindow = document.getElementById("gamewindow");
            gamewindow.classList.add('show');
        },
        gameclose(){
            var gamewindow = document.getElementById("gamewindow");
            gamewindow.classList.remove('show');
        },
        staffopen(){
            var gamewindow = document.getElementById("staffwindow");
            gamewindow.classList.add('show');
        },
        staffclose(){
            var gamewindow = document.getElementById("staffwindow");
            gamewindow.classList.remove('show');
        }
    },
    computed:{
        endbg:function(){
            if(this.heart>9){
                return 'goodbg';
            }else if(this.heart>6 && this.heart<=9){
                return 'normalbg';
            }else{
                return 'badbg';
            }
        }
    }
}
</script>
<style>
#section-end p{
    margin: 0;
}
#section-end.goodbg{
    background-image: url(../assets/img/bg_end_good.jpg);
}
#section-end.normalbg{
    background-image: url(../assets/img/bg_end_normal.jpg);
}
#section-end.badbg{
    background-image: url(../assets/img/bg_end_bad.jpg);
}
#section-end a,
#section-end a:hover{
    text-decoration: none;
}
#section-end .end-block{
    position: absolute;
    width: 340px;
    height: calc(100% - 50px);
    right: 30px;
    top: 25px;
}
#section-end .end-block .end-text{
    background-color: #fff;
    border-radius: 20px;
    overflow: hidden;
}
#section-end .end-block .end-text .title{
    padding: 15px;
    font-weight: bold;
    background-color: #f0f0f0;
}
#section-end .end-block .end-text .title h4{
    margin: 0;
    margin-bottom: 20px;
    font-size: 30px;
    color: #ea6e24;
    text-align: center;
}
#section-end .end-block .end-text .title p{
    font-weight: bold;
    font-size: 22px;
    color: #414141;
}
#section-end .end-block .end-text .again{
    display: block;
    padding: 20px;
    text-align: center;
}
#section-end .end-block .end-text .again h5{
    margin: 0;
    font-weight: bold;
    font-size: 30px;
}
#section-end .end-block .end-text .again h5 img{
    vertical-align: unset;
    width: 26px;
}
#section-end .end-block .end-text .again p{
    font-size: 22px;
    font-weight: bold;
    color: #414141;
}
#section-end .end-block .end-links{
    display: flex;
    padding: 10px 0;
}
#section-end .end-block .end-links .right{
    width: 215px;
    padding: 5px;
}
#section-end .end-block .end-links .left{
    width: 125px;
    padding: 5px;
}

#section-end .end-block .end-links .right .link{
    display: block;
    width: 100%;
    padding: 10px;
    margin-bottom: 12px;
    font-size: 22px;
    font-weight: bold;
    color: #414141;
    background-color: #ffffff;
    border: none;
    border-radius: 15px;
    box-shadow: inset 0 0 10px #cbc9c8;
    text-align: center;
}
#section-end .end-block .end-links .right .fb{
    display: block;
    position: relative;
    background-color: #65a1df;
    color: #FFF;
    text-align: center;
    font-size: 18px;
    padding: 13px;
    padding-left: 60px;
    border-radius: 15px;
    overflow: hidden;
    font-weight: bold;
    letter-spacing: 2px;
}
#section-end .end-block .end-links .right .fb img{
    position: absolute;
    left: 0;
    top: 0;
    height: 50px;
    width: 50px;
}
#section-end .end-block .end-links .left .muzik{
    display: block;
    width: 115px;
    height: 115px;
    margin-top: 0px;
    margin-bottom: 8px;
    border-radius: 15px;
    overflow: hidden;
}
#section-end .end-block .end-links .left .link{
    display: block;
    width: 100%;
    padding: 14px;
    letter-spacing: 2px;
    font-size: 18px;
    text-align: center;
    font-weight: bold;
    color: #ffffff;
    background-color: #4c4c4c;
    /* border: 3px solid #c9c9c9; */
    border-radius: 15px;
    text-shadow: 0 1px 4px #592929;
    box-shadow: inset 0 0 10px #787878;
}
#section-end .answer-block{
    position: absolute;
    width: 100%;
    max-height: 0;
    height: 100%;
    top: -200px;
    background-color: rgba(42, 34, 31, 0.8);
    border-bottom-left-radius: 100%;
    border-bottom-right-radius: 100%;
    padding-bottom: 200px;
    opacity: 0;
    overflow: hidden;
    transition-duration: .3s;
}
#section-end .answer-block.show{
    max-height: 100%;
    top: 0;
    padding-bottom: 0;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    opacity: 1;
}
#section-end .answer-block .cross{
    position: absolute;
    z-index: 10;
    right: 165px;
    top: 15px;
    padding: 12px;
    background-color: #dd6a6a;
    border-radius: 100%;
    cursor: pointer;
}
#section-end .answer-block .cross img{
    width: 20px;
    height: 20px;
    vertical-align: bottom;
}
#section-end .answer-block .game_answer{
    position: absolute;
    width: 600px;
    height: 480px;
    top: 30px;
    left: 180px;
    background-color: rgba(42, 34, 31, 0.8);
    padding: 15px;
    overflow-y: scroll;
    border: 1px solid #a8a8a8;
    border-radius: 15px;
}
#section-end .answer-block .game_answer .question_outer{
    display: flex;
    padding: 10px;
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 15px;
    margin-bottom: 10px;
    filter: grayscale(1) contrast(0.5);
}
#section-end .answer-block .game_answer .question_outer.done{
    filter: none;
}
#section-end .answer-block .game_answer .question_outer .img{
    border-radius: 100%;
    overflow: hidden;
    height: 100px;
    width: 100px;
    margin-right: 20px;
}
#section-end .answer-block .game_answer .question_outer .content{
    width: 400px;
}
#section-end .answer-block .game_answer .question_outer .content h6{
    margin: 0;
    margin-bottom: 15px;
    font-size: 22px;
}
#section-end .answer-block .game_answer .question_outer .content p{
    font-size: 20px;
}
#section-end .staff-block{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(42, 34, 31, 0.8);
    padding: 60px 80px;
    transition-duration: 0.3s;
    right: -100%;
}
#section-end .staff-block.show{
    right: 0;
}
#section-end .staff-block .cross{
    text-align: center;
}
#section-end .staff-block h6{
    font-size: 30px;
    margin: 0;
    margin-bottom: 35px;
}
#section-end .staff-block h6{
    margin: 20px 0;
    color: #FFF;
    text-align: center;
}
#section-end .staff-block .text{
    display: flex;
    justify-content: center;
    color: #FFF;
    width: 100%;
    line-height: 36px;
    font-size: 20px;
}
#section-end .staff-block .text .left{
    width: 50%;
    padding: 0 30px;
    border-right: 1px solid #FFF;
    text-align: right;
}
#section-end .staff-block .text .right{
    width: 50%;
    padding: 0 30px;
}
.bounce-enter-active {
    animation: bounce-in .5s;
}
.bounce-leave-active {
    animation: bounce-in .5s reverse;
}
@keyframes bounce-in {
    0% {
        transform: scale(0);
    }
    50% {
        transform: scale(1.2);
    }
    100% {
        transform: scale(1);
    }
}
</style>